.TodoItem{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  padding: .5em;
}

.TodoItem > .title{
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-left: 10px;
}

.TodoItem .deleteBtn{
  padding: 3px 5px;
  font-size: 14px;
  border-radius: 3px;
  background: rgba(0,0,0,0.3);
  color: #fff;
  font-weight: bold;
}
.TodoItem .deleteBtn:focus{
  outline: none;
  border: 1px solid lightgray;
}


